@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
}
p
{
  font-weight: 300;
  color: #111;
}
.banner
{
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(/portfolio/static/media/bg.fc8cc4f0.jpg);
  background-size: cover;
}
.banner .content
{
  max-width: 900px;
  text-align: center;
}
.banner .content h2
{
  font-size: 5em;
  color: #fff;
}
.banner .content p
{
  font-size: 1em;
  color: #fff;
}
.btnn
{
  font-size: 1em;
  color: #fff;
  background: #ff0157;
  display: inline-block;
  padding: 10px 30px;
  margin-top: 20px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  transition: 0.5s;
  border-radius: 5px;
}
.btnn:hover
{
  letter-spacing: 6px;
  color: #fff;
}
.codebtn
{
  font-size: 1em;
  color: rgb(0, 0, 0);
  background: #f3f3f3;
  display: inline-block;
  padding: 10px 30px;
  margin-top: 20px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  transition: 0.5s;
  border-radius: 5px;
}
.codebtn:hover
{
  letter-spacing: 6px;
  color: #ff0157;
}
.skillsbox
{
  padding: 20px 15px 12px 15px;
  background:#fff;
  box-shadow: 0 15px 35px rgba(0,0,0,0.1);
  margin-bottom: 10px;
  margin-top: 20px;
}
.skillsbox h3
{
  color: #111;
  font-size: 1.2em;
  font-weight: 500;
}
.linkcodebtn
{
  font-size: 1em;
  color: rgb(0, 0, 0);
  background: #f3f3f3;
  display: inline-block;
  padding: 15px 30px;
  margin-top: 20px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  transition: 0.5s;
  border-radius: 5px;
}
.linkcodebtn:hover
{
  letter-spacing: 6px;
  color: #ff0157;
}
header
{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 100px;
  z-index: 10000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.5s;
}
header.sticky
{
  background: #fff;
  padding: 10px 100px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.05);
}
header .logo
{
  color: #fff;
  font-weight: 700;
  font-size: 2em;
  text-decoration: none;
}
header.sticky .logo
{
  color: #111;
}
header .logo span
{
  color: #ff0157;
}
header .navigation
{
  position: relative;
  display: flex;
}
header .navigation li
{
  list-style: none;
  margin-left: 30px;
}
header .navigation li a
{
  text-decoration: none;
  color: #fff;
  font-weight: 300;
}

header.sticky .navigation li a
{
  color:#111;
}
header .navigation li a:hover
{
  color:#ff0157;
}
.section
{
  padding: 100px;
}
.row
{
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.row .col50
{
  position: relative;
  width: 48%;
}
.titleText
{
  color: #111;
  font-size: 2em;
  font-weight: 300;
}
.titleText span
{
 color: #ff0157;
 font-weight: 700;
 font-size: 1.5em; 
}
.row .col50 .imgBx
{
  position: relative;
  width: 100%;
  /*min-height: 300px;*/
  height: 100%;
}
.row .col50 .imgBx img
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.Projectbox
{ padding: 30px;
  background:#fff;
  box-shadow: 0 15px 35px rgba(0,0,0,0.1);
  margin-bottom: 10px;
  margin-top: 20px;
}
.Projectbox h3
{
  color: #111;
  font-size: 1.2em;
  font-weight: 500;
}



.contact
{
  background: url(/portfolio/static/media/bg3.3c991aea.jpg);
  background-size: cover;
}
.contactForm
{
  padding: 75px 50px;
  background:#fff;
  box-shadow: 0 15px 50px rgba(0,0,0,0.1);
  max-width: 500px;
  margin-top: 50px;
}
.contactForm h3
{
  color: #111;
  font-size: 1.2em;
  margin-bottom: 20px;
  font-weight: 500;
}
.contactForm .inputBox
{
  position: relative;
  width: 100%;
  margin-bottom: 20px;
}
.contactForm .inputBox input,
.contactForm .inputBox textarea
{
  width: 100%;
  border:1px solid #555;
  padding: 10px;
  color:#111;
  outline: none;
  font-size: 16px;
  font-weight: 300;
  resize: none;
}
.contactForm .inputBox input[type="submit"]
{
  font-size: 1em;
  color: #fff;
  background: #ff0157;
  display: inline-block;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  transition: 0.5s;
  max-width: 100px;
  font-weight: 500;
  border: none;
  cursor: pointer;
}
.contactForm .inputBox input[type="submit"]:hover
{
  letter-spacing: 3px;
  max-width: 105px;
}

.contactForm .inputBox input[type="button"]
{
  font-size: 1em;
  color: #000;
  background: #01ffb7;
  display: inline-block;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  transition: 0.5s;
  max-width: 180px;
  font-weight: 500;
  border:none;
  cursor: default;
}

.btn-sent {
  margin: 0 20px;
}

@media (max-width: 462px) { 

  .contactForm .inputBox input[type="submit"] {
    max-width: 80px;
  }

  .contactForm .inputBox input[type="button"] {
    max-width: 150px;
    letter-spacing: 1px;
  }

  .btn-sent {
    margin: 0 0 0 5px;
  }
  
}

.oneline {
  display:inline;
}


.copyrightText
{
  padding: 8px 40px;
  border-top: 1px solid rgba(0,0,0,0.1);
  text-align: center;
}
.copyrightText p
{
  color:#333;
}
.copyrightText a
{
  color: #ff0157;
  font-weight: 500;
  text-decoration: none;
}

@media (max-width:991px)
{
  header,
  header.sticky
  {
    padding: 10px 20px;
  }
  header .navigation
  {
    display: none;
  }
  header .navigation.active
  {
    width: 100%;
    height: calc(100% - 68px);
    position: fixed;
    top: 68px;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background:#fff;
  }
  header .navigation li
  {
    margin-left: 0;
  }
  header .navigation li a
  {
    color:#111;
    font-size: 1.6em;
  }
  .menuToggle
  {
    position: relative;
    width: 40px;
    height: 40px;
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAC8AAAAfCAYAAAB3XZQBAAAABGdBTUEAALGPC/xhBQAAACBjSFJNAAB6JgAAgIQAAPoAAACA6AAAdTAAAOpgAAA6mAAAF3CculE8AAAABmJLR0QAAAAAAAD5Q7t/AAAACXBIWXMAAAsSAAALEgHS3X78AAAAM0lEQVRYw+3WsREAIAzEMML+Oz8FQxAu0gQuXUmyPrVfB4gHALjKVYofFg8AbRkz8dPiD+qQDAquHlwPAAAAAElFTkSuQmCC);
    background-size: 30px;
    background-repeat: no-repeat;
    background-position: center;
    cursor:pointer;
  }
  .menuToggle.active
  {
    background: url(data:image/png;base64,);
    background-size: 25px;
    background-repeat: no-repeat;
    background-position: center;
  }
  header.sticky .menuToggle
  {
    -webkit-filter: invert(1);
            filter: invert(1);
  }
  .section
  {
    padding: 20px;
  }
  .banner .content h2
  {
    font-size: 3em;
    color: #fff;
  }
  .row
  {
    flex-direction: column;
  }
  .row .col50
  {
    position: relative;
    width: 100%;
  }
  .row .col50 .imgBx
  {
    height:250px;
    margin-top: 20px;
  }
  .title
  {
    text-align: center;
  }
  .titleText
  {
    font-size: 1.8em;
    line-height: 1.5em;
    margin-bottom: 15px;
    font-weight: 300;
  }
  .contactForm
  {
    padding: 35px 40px;
    margin-top: 20px;
    margin-bottom: 30px;
  }
}
@media (max-width:480px)
{
  .banner .content h2
  {
    font-size:2.2em;
    color: #fff;
  }
  
}
